.flexcontainer {
  display: flex;
  height: 255px;
  justify-content: flex-start;
  margin-bottom: 20px;
  width: 50%;
}



.containerimage {
  height: 255px;
  object-fit: cover;
  width: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin: 30px auto 50px;
  display: inline-block;
}


