.button {
  background-color: #c71585;
  color: white;
  font-weight: bold;
  border-radius: 10px;
  font-size: 10px;
  padding: 10px;
}

.button:hover {
  background-color: #CFFFE5;
}

h1 {
  margin-top: 25px;
